* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-item {
  margin-bottom: 10px;
  display: flex;
  font-size: 20px;
  font-weight: bold;
}
.country-flag {
  padding-right: 10px;
  display: block;
  width: 70px;
}

.country-name {
  display: flex;
}
.country-style {
  font-weight: normal;
}
